.categories {
  &__title {
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);

    gap: var(--grid-gap);

    @include for-width (-tablet) {
      grid-template-columns: repeat(3, 1fr);
      place-items: center;
    }

    // @include for-width (-mobile-md) {
    //   grid-template-columns: repeat(2, 1fr);
    //   place-items: center;
    // }
  }

  &__item {
    position: relative;


    picture {
      overflow: hidden;
      margin-bottom: 25px;
      border-radius: 100vw;
      // height: 180px;
      // width: 180px;
    }

    a {
      &::before {
        content: "";
        position: absolute;
        z-index: 5;
        inset: 0;
      }
    }

    img {
      transition: transform 0.3s linear;
    }

    &:hover {
      h3 {
        color: var(--color-green);
      }

      img {
        transform: scale(1.05);
      }
    }
  }
}
